"use client";

import React, { useEffect, useState } from "react";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import {
    DollarSign,
    CheckCircle,
    AlertCircle,
    Mail,
    Loader2,
    Search,
    Filter,
} from "lucide-react";
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import { fetchPayments, sendReminderEmail } from "@/actions/payment";

interface Payment {
    id: string;
    bookingId: string;
    studioName: string;
    userName: string;
    userEmail: string;
    amount: number;
    status: "paid" | "pending" | "failed";
    paymentMethod: "stripe" | "credit" | "other";
    date: string;
    dueDate?: string;
}

export default function PaymentsPage() {
    const [payments, setPayments] = useState<Payment[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [filter, setFilter] = useState<string>("all");
    const [searchTerm, setSearchTerm] = useState("");
    const [sendingReminder, setSendingReminder] = useState<string | null>(null);

    const loadPayments = async () => {
        setIsLoading(true);
        const data = await fetchPayments();
        setPayments(data);
        setIsLoading(false);
    };

    useEffect(() => {
        loadPayments();
    }, []);

    const handleSendReminder = async (payment: Payment) => {
        if (!confirm(`${payment.userEmail} に催促メールを送信しますか？`)) return;
        setSendingReminder(payment.id);
        await sendReminderEmail(payment.id, payment.userEmail);
        setSendingReminder(null);
        alert("催促メールを送信しました");
    };

    const filteredPayments = payments.filter((payment) => {
        const matchesFilter =
            filter === "all" ||
            (filter === "pending" && payment.status === "pending") ||
            (filter === "paid" && payment.status === "paid") ||
            (filter === "failed" && payment.status === "failed");

        const matchesSearch =
            searchTerm === "" ||
            payment.studioName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            payment.userName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            payment.userEmail.toLowerCase().includes(searchTerm.toLowerCase());

        return matchesFilter && matchesSearch;
    });

    const totalRevenue = payments
        .filter((p) => p.status === "paid")
        .reduce((sum, p) => sum + p.amount, 0);
    const pendingAmount = payments
        .filter((p) => p.status === "pending")
        .reduce((sum, p) => sum + p.amount, 0);
    const pendingCount = payments.filter((p) => p.status === "pending").length;

    if (isLoading) {
        return (
            <div className="min-h-screen bg-black text-white flex items-center justify-center">
                <Loader2 className="animate-spin mr-2" /> Loading...
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-black text-white p-10 font-sans">
            <div className="max-w-7xl mx-auto">
                <div className="mb-8">
                    <h1 className="text-3xl font-bold text-cyan-400 mb-2">決済管理</h1>
                    <p className="text-gray-400">
                        Stripe連携・入金確認・催促メール送信
                    </p>
                </div>

                {/* Stats Cards */}
                <div className="grid md:grid-cols-3 gap-6 mb-8">
                    <Card className="bg-slate-900 border-white/10">
                        <CardHeader className="flex flex-row items-center justify-between pb-2">
                            <CardTitle className="text-sm font-medium text-gray-400">
                                総売上
                            </CardTitle>
                            <DollarSign className="h-4 w-4 text-cyan-400" />
                        </CardHeader>
                        <CardContent>
                            <div className="text-2xl font-bold text-white">
                                ¥{totalRevenue.toLocaleString()}
                            </div>
                            <p className="text-xs text-gray-500 mt-1">
                                {payments.filter((p) => p.status === "paid").length} 件の入金
                            </p>
                        </CardContent>
                    </Card>

                    <Card className="bg-slate-900 border-white/10">
                        <CardHeader className="flex flex-row items-center justify-between pb-2">
                            <CardTitle className="text-sm font-medium text-gray-400">
                                未入金額
                            </CardTitle>
                            <AlertCircle className="h-4 w-4 text-yellow-400" />
                        </CardHeader>
                        <CardContent>
                            <div className="text-2xl font-bold text-yellow-400">
                                ¥{pendingAmount.toLocaleString()}
                            </div>
                            <p className="text-xs text-gray-500 mt-1">
                                {pendingCount} 件の未入金
                            </p>
                        </CardContent>
                    </Card>

                    <Card className="bg-slate-900 border-white/10">
                        <CardHeader className="flex flex-row items-center justify-between pb-2">
                            <CardTitle className="text-sm font-medium text-gray-400">
                                成功率
                            </CardTitle>
                            <CheckCircle className="h-4 w-4 text-green-400" />
                        </CardHeader>
                        <CardContent>
                            <div className="text-2xl font-bold text-white">
                                {payments.length > 0
                                    ? Math.round(
                                        (payments.filter((p) => p.status === "paid").length /
                                            payments.length) *
                                        100
                                    )
                                    : 0}
                                %
                            </div>
                            <p className="text-xs text-gray-500 mt-1">
                                全{payments.length}件中
                            </p>
                        </CardContent>
                    </Card>
                </div>

                {/* Filters */}
                <div className="flex flex-col md:flex-row gap-4 mb-6">
                    <div className="flex-1 relative">
                        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-500" />
                        <Input
                            placeholder="スタジオ名、ユーザー名、メールで検索..."
                            className="pl-10 bg-slate-900 border-white/10 text-white"
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                        />
                    </div>
                    <Select value={filter} onValueChange={setFilter}>
                        <SelectTrigger className="w-full md:w-[200px] bg-slate-900 border-white/10 text-white">
                            <Filter className="h-4 w-4 mr-2" />
                            <SelectValue placeholder="フィルター" />
                        </SelectTrigger>
                        <SelectContent className="bg-slate-900 border-white/10 text-white">
                            <SelectItem value="all">すべて</SelectItem>
                            <SelectItem value="paid">入金済み</SelectItem>
                            <SelectItem value="pending">未入金</SelectItem>
                            <SelectItem value="failed">失敗</SelectItem>
                        </SelectContent>
                    </Select>
                </div>

                {/* Payments Table */}
                <Card className="bg-slate-900 border-white/10">
                    <CardContent className="p-0">
                        <Table>
                            <TableHeader className="bg-white/5">
                                <TableRow className="border-white/10 hover:bg-transparent">
                                    <TableHead className="text-cyan-400">予約ID</TableHead>
                                    <TableHead className="text-cyan-400">スタジオ</TableHead>
                                    <TableHead className="text-cyan-400">ユーザー</TableHead>
                                    <TableHead className="text-cyan-400">金額</TableHead>
                                    <TableHead className="text-cyan-400">支払方法</TableHead>
                                    <TableHead className="text-cyan-400">ステータス</TableHead>
                                    <TableHead className="text-cyan-400">日時</TableHead>
                                    <TableHead className="text-cyan-400">アクション</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {filteredPayments.length === 0 ? (
                                    <TableRow>
                                        <TableCell
                                            colSpan={8}
                                            className="text-center py-10 text-gray-500"
                                        >
                                            該当する決済データがありません
                                        </TableCell>
                                    </TableRow>
                                ) : (
                                    filteredPayments.map((payment) => (
                                        <TableRow
                                            key={payment.id}
                                            className="border-white/10 hover:bg-white/5"
                                        >
                                            <TableCell className="font-mono text-sm text-gray-400">
                                                {payment.bookingId.substring(0, 8)}...
                                            </TableCell>
                                            <TableCell className="text-white">
                                                {payment.studioName}
                                            </TableCell>
                                            <TableCell>
                                                <div className="text-white">{payment.userName}</div>
                                                <div className="text-xs text-gray-500">
                                                    {payment.userEmail}
                                                </div>
                                            </TableCell>
                                            <TableCell className="font-medium text-white">
                                                ¥{payment.amount.toLocaleString()}
                                            </TableCell>
                                            <TableCell>
                                                <Badge
                                                    variant="outline"
                                                    className="border-cyan-500/30 text-cyan-400"
                                                >
                                                    {payment.paymentMethod}
                                                </Badge>
                                            </TableCell>
                                            <TableCell>
                                                {payment.status === "paid" && (
                                                    <Badge className="bg-green-900/30 text-green-400 border-green-500/30">
                                                        <CheckCircle className="h-3 w-3 mr-1" />
                                                        入金済み
                                                    </Badge>
                                                )}
                                                {payment.status === "pending" && (
                                                    <Badge className="bg-yellow-900/30 text-yellow-400 border-yellow-500/30">
                                                        <AlertCircle className="h-3 w-3 mr-1" />
                                                        未入金
                                                    </Badge>
                                                )}
                                                {payment.status === "failed" && (
                                                    <Badge className="bg-red-900/30 text-red-400 border-red-500/30">
                                                        失敗
                                                    </Badge>
                                                )}
                                            </TableCell>
                                            <TableCell className="text-gray-400 text-sm">
                                                {payment.date}
                                            </TableCell>
                                            <TableCell>
                                                {payment.status === "pending" && (
                                                    <Button
                                                        size="sm"
                                                        variant="outline"
                                                        className="border-cyan-500/30 text-cyan-400 hover:bg-cyan-900/20"
                                                        onClick={() => handleSendReminder(payment)}
                                                        disabled={sendingReminder === payment.id}
                                                    >
                                                        {sendingReminder === payment.id ? (
                                                            <Loader2 className="h-3 w-3 mr-1 animate-spin" />
                                                        ) : (
                                                            <Mail className="h-3 w-3 mr-1" />
                                                        )}
                                                        催促
                                                    </Button>
                                                )}
                                            </TableCell>
                                        </TableRow>
                                    ))
                                )}
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}
